XHeader, XButton, Tab, TabItem, Swipeout, SwipeoutButton, SwipeoutItem, Icon, Group, Cell, CellBox, CellFormPreview
},
data() {
return {
routeName: this.$route.name,
contantShow: true, showContent001: false, showContent004: false,
gradeList: [
{ gId: 'G1', gName: '', showContent: false },
{ gId: 'G2', gName: '', showContent: false },
{ gId: 'G3', gName: '', showContent: false },
],
teacherShowList: [
{ id: 1, name: '1', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 2, name: '2', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/xiaoji.jpg' },
{ id: 3, name: '3', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 4, name: '4', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
],
studentShowList: [
{ id: 1, name: '1', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 2, name: '2', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/xiaoji.jpg' },
{ id: 3, name: '3', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 4, name: '4', leave: '', className: '', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
],


}
},
methods: {
search() {
console.log('search')
this.$router.push({ path: '/check/searchCheck' });
},
onButtonClick(type) {
console.log('DELid ' + type)
},
goDetail(type) {
console.log('ADDid ' + type)

},
handleEvents(type) {
console.log('delId: ', type)
},
},
created() {
document.title = this.$route.name
$(function () {//

var aa = [];
var i = 0;
var src = [];
aa = $('img');
for (i = 0; i < aa.length; i++) {
src[i] = aa[i].src;    //src
}

$('img').click(function () {
var index = $('img').index(this);
imagePreview(src[index], src);
});
});
function imagePreview(curSrc, srcList) {

if (!curSrc || !srcList || srcList.length == 0) {
return;
}

WeixinJSBridge.invoke('imagePreview', {
'current': curSrc,
'urls': srcList
});
};
}
}



